<template>
	<div id="tokendeployed">
		<strong style="font-size:20px;width:auto;">代币</strong>
		<hr style="width:75%;" size="1" color="#d9d9d9" />
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;margin-top:30px;">
			<strong>货币名称</strong><span v-text="contract.name"></span>
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;margin-top:30px;">
			<strong>balance</strong><span v-text="contract.balance"></span>
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>标记</strong><span v-text="contract.symbol"></span>
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>小数</strong><span v-text="contract.decimals"></span>
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>总数量</strong><span v-text="contract.totalSupply"></span>
		</div>
		<hr style="width:75%;" size="1" color="#d9d9d9" />
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;padding:10px;">
			<strong style="font-size:20px;">资产相关</strong>
		</div>
		<table style="width:75%" class="table table-striped table-bordered table-hover">
			<thead style="background-color:#f9f9f9;">
				<tr>
					<th style="width:30%;">地址</th>
					<th style="width:70%;">资产</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="(address, index) in Object.keys(contract.balanceOf)" :key="index">
					<td v-text="address"></td>
					<td v-text="contract.balanceOf[address]"></td>
				</tr>
			</tbody>
		</table>
		<hr style="width:75%;" size="1" color="#d9d9d9" />
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;padding:10px;">
			<strong style="font-size:20px;">津贴</strong>
		</div>
		<table style="width:75%" class="table table-striped table-bordered table-hover">
			<thead style="background-color:#f9f9f9;">
				<tr>
					<th style="width:40%;">来源</th>
					<th style="width:40%;">流向</th>
					<th style="width:20%;">津贴</th>
				</tr>
			</thead>
			<tbody v-for="(from, index) in Object.keys(contract.approve)" :key="index">
				<tr v-for="(to, index) in Object.keys(contract.approve[from])" :key="index">
					<td v-text="from"></td>
					<td v-text="to"></td>
					<td v-text="contract.approve[from][to]"></td>
				</tr>
			</tbody>
		</table>
		<hr style="width:75%;" size="1" color="#d9d9d9" />
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;justify-content:flex-end">
			<el-button @click="freezeShow = true" class="btn btn-primary">freeze</el-button>
			<el-button id='approveShowButton' @click="approveShow = true" class="btn btn-primary">allow</el-button>
			<el-button id='mintShowButton' @click="mintShow = true" class="btn btn-primary">mint</el-button>
		</div>
		<el-dialog title="freeze" :visible.sync="freezeShow" width="50%" :show-close="false" :center="true" class="dialog">
			<div class="main">
				<div class="lside">target</div>
				<div class="rside">
					<el-input :value="target"></el-input>
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">gas</div>
				<div class="rside">
					<el-input v-model="gas" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">password</div>
				<div class="rside">
					<el-input v-model="password" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">unlockDurationSec</div>
				<div class="rside">
					<el-input v-model="unlockDurationSec" />
				</div>
			</div>
			<div class="footer">
				<el-button @click="freeze" class="btn btn-primary">freeze</el-button>
				<el-button @click="unfreeze" class="btn btn-primary">unfreeze</el-button>
				<el-button @click="() => {target='';password='';unlockDurationSec=10;freezeShow=false;}" class="btn btn-primary">取消</el-button>
			</div>
			<div class="line" style="height: 1px;"></div>
		</el-dialog>
		<el-dialog title="approve" :visible.sync="approveShow" width="50%" :shou-close="false" :center="true" class="dialog">
			<div class="main">
				<div class="lside">spender</div>
				<div class="rside">
					<el-input id="approveSpender" v-model="spender" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">数量</div>
				<div class="rside">
					<el-input id="approveAmmount" v-model="approveAmmount" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">gas</div>
				<div class="rside">
					<el-input id="approveGas" v-model="gas" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">支付密码</div>
				<div class="rside">
					<el-input id="approvePassword" v-model="password" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">unlockDurationSec</div>
				<div class="rside">
					<el-input id="approveUnlockDurationSec" v-model="unlockDurationSec" />
				</div>
			</div>
			<div class="footer">
				<el-button id="approveButton" @click="approve" class="btn btn-primary">确定</el-button>
				<el-button id="approveCancelButton" @click="() => {spender='';approveAmmount=0;password='';unlockDurationSec=10;approveShow=false;}" class="btn btn-primary">取消</el-button>
			</div>
			<div class="line" style="height: 1px;"></div>
		</el-dialog>
		<el-dialog title="mint" :visible.sync="mintShow" width="50%" :shou-close="false" :center="true" class="dialog">
			<div class="main">
				<div class="lside">流向</div>
				<div class="rside">
					<el-input id="mintMintTo" v-model="mintTo" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">数量</div>
				<div class="rside">
					<el-input id="mintMintAmmount" v-model="mintAmmount" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">gas</div>
				<div class="rside">
					<el-input id="mintGas" v-model="gas" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">支付密码</div>
				<div class="rside">
					<el-input id="mintPassword" v-model="password" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">unlockDurationSec</div>
				<div class="rside">
					<el-input id="unlockDurationSec" v-model="unlockDurationSec" />
				</div>
			</div>
			<div class="footer">
				<el-button id="mintButton" @click="mint" class="btn btn-primary">确定</el-button>
				<el-button id="mintCancelButton" @click="() => {mintTo='';mintAmmount=0;password='';unlockDurationSec=10;mintShow=false;}" class="btn btn-primary">取消</el-button>
			</div>
			<div class="line" style="height: 1px;"></div>
		</el-dialog>
	</div>
</template>

<script>
	import {getItem} from '@/localstorage/index'
	import { gmToken, getArtifact } from '../contract'
	export default {
		name: 'CrowdFunding',
		data() {
			return {
				contract: {
					name: '',
					balance: 0,
					symbol: '',
					decimals: 0,
					totalSupply: 0,
					balanceOf: {},
					approve: {}
				},
				approveShow: false,
				spender: '',
				approveAmmount: 0,
				mintShow: false,
				mintTo: '',
				mintAmmount: 0,
				freezeShow: false,
				target: '',
				password: '',
				unlockDurationSec: 10,
				gas: 999999
			}
		},
		created: function() {
			let self = this
			$.extend(self.contract, self.$route.query)
			getArtifact(self.axiosInstance, self.contract.address, (err, artifact) => {
				if(err) {
					self.msg_error('getArtifact error', err)
					return
				}

				gmToken.watch(self, artifact, function() {

				})

				gmToken.name(getItem('address'), artifact, function(err, name) {
					if(err) {
						self.msg_error('getItem error', err)
						return
					}
					self.contract.name = name
				})

				gmToken.balanceOf(getItem('address'), artifact, getItem('address'), function(err, balance) {
					if(err) {
						self.msg_error('balanceOf error', err)
						return
					}
					self.contract.balance = parseInt(balance)
				})

				gmToken.symbol(getItem('address'), artifact, function(err, symbol) {
					if(err) {
						self.msg_error('symbol error', err)
						return
					}
					self.contract.symbol = symbol
				})

				gmToken.decimals(getItem('address'), artifact, function(err, decimals) {
					if(err) {
						self.msg_error('decimals error', err)
						return
					}
					self.contract.decimals = parseInt(decimals)
				})

				gmToken.totalSupply(getItem('address'), artifact, function(err, totalSupply) {
					if(err) {
						self.msg_error('totalSupply error', err)
						return
					}
					self.contract.totalSupply = parseInt(totalSupply)
				})
			})
		},
		methods: {
			unfreeze: function() {
				let self = this

				getArtifact(self.axiosInstance, self.contract.address, (err, artifact) => {
					if(err) {
						self.msg_error('getArtifact error', err)
						return
					}
					gmToken.unfreeze(getItem('address'), self.password, self.unlockDurationSec, artifact, self.target, self.gas, function(err, result) {
						if(err) {
							self.msg_error('unfreeze error', err)
							return
						}
						self.target = ''
						self.password = '';
						self.msg_success('unfreeze success', '')
					})
				})
			},
			freeze: function() {
				let self = this

				getArtifact(self.axiosInstance, self.contract.address, (err, artifact) => {
					if(err) {
						self.msg_error('getArtifact error', err)
						return
					}
					gmToken.freeze(getItem('address'), self.password, self.unlockDurationSec, artifact, self.target, self.gas, function(err, response) {
						if(err) {
							
							self.msg_error('freeze error', err)
							return
						}
						self.target = ''
						self.password = '';
						self.msg_success('free success', '')
					})
				})
			},
			approve: function() {
				let self = this

				getArtifact(self.axiosInstance, self.contract.address, (err, artifact) => {
					if(err) {
						self.msg_error('getArtifact error', err)
						return
					}
					gmToken.approve(getItem('address'), self.password, self.unlockDurationSec, artifact, self.spender, self.approveAmmount, self.gas, function(err, response) {
						if(err) {
							
							self.msg_error('approve error', err)
							return
						}
						self.spender = ''
						self.approveAmmount = 0;
						self.password = '';
					})
				})
			},

			mint: function() {
				let self = this

				getArtifact(self.axiosInstance, self.contract.address, (err, artifact) => {
					if(err) {
						self.msg_error('getArtifact error', err)
						return
					}
					gmToken.mint(getItem('address'), self.password, self.unlockDurationSec, artifact, self.mintTo, self.mintAmmount, self.gas, function(err, response) {
						if(err) {
							self.msg_error('mint error', err)
							return
						}
						self.totalSupply += parseInt(self.mintAmmount)
						self.mintTo = '';
						self.mintAmmount = 0;
						self.password = '';
					})
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	#tokendeployed {
		display: flex;
		display: -webkit-flex;
		flex-direction: column;
		align-items: center;
		.main-box {
			position: fixed;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			z-index: 9999;
		}
	}
	
	strong {
		width: 300px;
		font-size: 15px;
	}
	
	span {
		width: 100%;
		height: 40px;
		background-color: rgb(255, 255, 255);
		border-radius: 4px;
		border-color: rgb(220, 223, 230);
		border-width: 1px;
		border-style: solid;
		color: rgb(96, 98, 102);
		display: inline-block;
		line-height: 40px;
		padding-left: 15px;
		padding-right: 15px;
	}
	
	div span {
		background-color: #f9f9f9;
		opacity: 1;
	}
	
	.balanceOf,
	.approve {
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		width: 100%;
	}
	
	.ibox-content {
		clear: both;
		margin-bottom: 0;
		padding: 15px 20px 20px;
		min-height: 48px;
		color: inherit;
		background-color: #fff;
		border-color: #e7eaec;
		-webkit-border-image: none;
		-o-border-image: none;
		border-image: none;
		border-style: solid solid none;
		border-width: 1px 0;
	}
	
	thead {
		display: table-header-group;
		vertical-align: middle;
		border-color: inherit;
	}
	
	.table-bordered {
		border: 1px solid #ddd;
	}
	
	.table {
		width: 100%;
		max-width: 100%;
		margin-bottom: 20px;
	}
	
	table {
		width: 100%;
		table-layout: fixed;
		background-color: transparent;
		border-collapse: collapse;
		border-spacing: 0;
	}
	
	.table-striped>tbody>tr:nth-of-type(odd) {
		background-color: #f9f9f9;
	}
	
	.btn-primary {
		color: rgb(255, 255, 255);
		background-color: rgb(186, 27, 33);
		border-color: rgb(186, 27, 33);
	}
	
	.btn {
		margin-bottom: 8px;
		line-height: 32px;
		font-size: 14px;
		padding: 0px 20px;
		border-radius: 3px;
		display: inline-block;
		margin-bottom: 0px;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.42857;
		text-align: center;
		white-space: nowrap;
		vertical-align: middle;
		cursor: pointer;
		user-select: none;
		background-image: none;
		padding: 6px 12px;
		border-width: 1px;
		border-style: solid;
		border-color: transparent;
		border-image: initial;
		border-radius: 4px;
	}
	
	.dialog {
		clear: both;
		.main {
			.lside {
				width: 25%;
				float: left;
				line-height: 40px;
				font-size: 16px;
				font-weight: bold;
				text-align: center;
			}
			.rside {
				width: 75%;
				float: left;
			}
		}
		.line {
			height: 10px;
			width: 100%;
			clear: both;
		}
		.footer {
			padding-top: 15px;
			margin-top: 10px;
			float: right;
		}
	}
</style>


