<template>
	<div id="app">
		<div class="content">
			<p class="tip-text" v-text="title"></p>
			<div id="login" class="" v-if="sinupLiginSwitch">
				<div class="input-box">
					<input id='loginUserName' type="text" autocomplete="off" autofocus="autofocus" placeholder="请输入用户名或邮箱" class="user-input" v-model="login_username">
				</div>
				<div class="input-box">
					<input id='loginPassword' type="password" placeholder="请输入密码" class="user-input" v-model="login_password" @keyup.13="login">
				</div>
				<div class="submit">
					<a id='loginButton' @click="login" class="btn">登陆</a>
				</div>
			</div>
			<div id="register" class="" v-else="sinupLiginSwitch">
				<div class="input-box">
					<input id='signupUsername' type="text" autocomplete="off" autofocus="autofocus" placeholder="请输入用户名或邮箱" class="user-input" v-model="register_username">
				</div>
				<div class="input-box">
					<input id='signupPassword' type="password" placeholder="请输入密码" class="user-input" v-model="register_password">
				</div>
				<div class="input-box">
					<input id='signupPasswordRepeat' type="password" placeholder="请输入确认密码" class="user-input" v-model="register_judge_password">
				</div>
				<div class="input-box">
					<input id='signupAddress' type="text" placeholder="input bind address" class="user-input" v-model="register_address" @keyup.13="register">
				</div>
				<div class="submit">
					<a id='aSignup' @click="register" class="btn">注册</a>
				</div>
			</div>
			<div id="change-box" class="change">
				<a id="change-box-button" class="btn" @click="sinupLiginSwitch = !sinupLiginSwitch" v-text="changeText"></a>
			</div>
		</div>
	</div>
</template>

<script>
	import { setItem, delItem, getItem } from './localstorage'
	import QS from 'qs';

	export default {
		name: 'loginPage',
		data() {
			return {
				title: '用户登陆',
				changeText: '没有账号?马上注册',
				login_username: '',
				login_password: '',
				register_username: '',
				register_password: '',
				register_judge_password: '',
				register_address: '',

				sinupLiginSwitch: true,
			}
		},
		watch: {
			sinupLiginSwitch() {
				if(this.sinupLiginSwitch == true) {
					this.title = '用户登陆',
						this.changeText = '没有账号?马上注册'
				} else {
					this.title = '用户注册',
						this.changeText = '已有账号?马上登陆'
				}
			}
		},
		methods: {
			login() {
				var self = this;

				if(!self.login_username || !self.login_password) {
					this.msg_error('登陆失败', '用户名不能为空！')
				} else {
					self.axiosInstance.post('/login', QS.stringify({
						username: self.login_username,
						password: self.login_password
					}, {
						allowDots: true
					})).then((response) => {
						if(response.data.ret != 0) {
							this.msg_error('登陆失败', '用户名或密码错误！')
							return
						} else {
							console.log(response.data)
							setItem('username', self.login_username)
							setItem('address', response.data.address)
							self.$router.push({ name: 'homePage' });
							this.msg_success('登陆成功', '用户:' + self.login_username + '欢迎您使用GmBc')
						}
					}).catch((err) => {
						console.log(err)
						this.msg_error('请求异常', '登陆失败，服务器未响应！')
					});
				}
			},
			register() {
				let self = this
				if(!self.register_username || !self.register_password) {
					this.msg_error('注册失败', '用户名或密码不能为空！')
				} else if(self.register_password != self.register_judge_password) {
					this.msg_error('注册失败', '两次输入的密码不一致！')
				} else {
					new Promise((resolve, reject) => {
						if(self.register_address !== '') {
							resolve(self.register_address)
							return
						}
						self.axiosContractInstance.get('/newAccount', {
							params: {
								password: self.register_password
							}
						}).then((response) => {
							if(response.data.code != 0) {
								reject(response.data.msg);
								return;
							}
							resolve(response.data.address);
						}).catch((err) => {
							reject(err);
						});
					}).then((address) => {
						setItem('address', address)
						return new Promise((resolve, reject) => {
							self.axiosInstance.post('/signup', QS.stringify({
								username: self.register_username,
								password: self.register_password,
								address: address
							}, {
								allowDots: true
							})).then((response) => {
								if(response.data.ret !== 0) {
									reject(response.data.msg);
									return;
								}
								resolve()
							}).catch((err) => {
								reject(err);
							});
						})
					}).then(() => {
						self.$router.push({
							name: 'homePage'
						});
						setItem('username', self.register_username)
						this.msg_success('登陆成功', '用户:' + self.login_username + '欢迎您使用GmBc')
					}).catch((err) => {
						console.log(err)
						this.msg_error('注册失败', err)
					})
				}
			}
		}
	}
</script>

<style lang="scss" scoped type="text/css">
	$theme-color: #2C3E50;
	$white-color: #FEFEFE;
	$input-border: #2C3E50 1px solid;
	$border-radius: 5px;
	#app {
		* {
			padding: 0;
		}
		.content {
			position: fixed;
			width: 432px;
			height: auto;
			left: 50%;
			top: 50%;
			margin: -200px 0px 0px -252px;
			background-color: $white-color;
			border-radius: $border-radius;
			overflow: hidden;
			border: $input-border;
			.tip-text {
				padding-top: 10px;
				font-weight: bold;
				font-size: 1.5em;
				margin: 0 auto;
				text-align: center;
				color: $theme-color;
			}
			.input-box {
				margin: 20px auto 0px auto;
				text-align: center;
			}
			.user-input {
				height: 50px;
				width: 300px;
				line-height: 50px;
				outline: none;
				background-color: #F9F9F9;
				padding-left: 10px;
				border: 1px solid #EEEEEE;
				border-radius: $border-radius;
				transition: 0.5s;
				&:focus {
					border: $input-border;
				}
			}
			.submit {
				margin-top: 30px;
				margin-bottom: 70px;
				a {
					display: block;
					;
					width: 80%;
					height: 50px;
					margin: 0 auto;
					background-color: $theme-color;
					line-height: 50px;
					text-align: center;
					text-decoration: none;
					color: $white-color;
					border-radius: $border-radius;
					outline: none;
					transition: 0.5s;
					&:hover {
						background-color: #466380;
					}
				}
			}
			#change-box {
				position: absolute;
				height: 50px;
				width: 100%;
				bottom: 0;
				background-color: $theme-color;
				color: $white-color;
				transition: 0.3s;
				&:hover {
					background: #466380;
					color: #CCCCCC;
				}
				a {
					display: block;
					line-height: 50px;
					text-align: center;
					color: $white-color;
				}
			}
		}
	}
</style>


