<template lang="html">
	<div>
		<div>
			<el-input
				v-model="innerSearchText" v-bind="inputAttrs"
				autofocus="true"
				ref="autocomplete"
				:placeholder="inputAttrs.placeholder || '请输入内容'"
				:disabled="inputAttrs.disabled || false"
				@blur="blur" @focus="focus"
				@input="inputChange"
				@keyup.enter="keyEnter"
				@keydown.tab="keyEnter"
				@keydown.up="keyUp"
				@keydown.down="keyDown" />
		</div>
		<div v-show="show">
			<div
				class="list-item"
				:class="{'list-item-active': cursor === index}"
				v-for="(item, index) in internalItems"
				:key="item.id"
				@click="onClickItem(item)"
				@mouseover="cursor = index">
				<div
					:is="componentItem"
					:item="item"></div>
			</div>
		</div>
	</div>
</template>

<script>
import Item from './Item.vue'
import utils from './js/utils.js'

export default {
	name: 'v-autocomplete',
	props: {
		searchText: {type: String, default: ''},
		componentItem: Item,
		minLen: { type: Number, default: utils.minLen },
		wait: { type: Number, default: utils.wait },
		getLabel: {
			type: Function,
			default: item => item
		},
		items: Array,
		inputAttrs: {}
	},
	data () {
		return {
			innerSearchText: this.searchText || '',
			selectedItem: null,
			showList: false,
			cursor: -1
		}
	},
	computed: {
		hasItems () {
			return !!this.internalItems.length
		},
		show () {
			return this.showList && this.hasItems
		},
		internalItems: {
			get: function () {
				return this.items || []
			},
			set: function () {

			}
		}
	},
	methods: {
		inputChange () {
			this.selectedItem = null
			this.showList = true
			this.cursor = -1
			utils.callUpdateItems(this.innerSearchText, this.updateItems)
			this.$emit('input-change', this.innerSearchText)
		},

		updateItems () {
			this.$emit('update-items', this.innerSearchText)
		},

		focus () {
			this.$emit('focus')

			this.showList = true
		},

		blur () {
			var self = this
			this.$emit('blur')

			setTimeout(() => { self.showList = false }, 200)
		},

		onClickItem (item) {
			this.onSelectItem(item)
		},

		onSelectItem (item) {
			if (item) {
				this.selectedItem = item
				this.internalItems = [item]
				this.innerSearchText = this.getLabel(item)
				this.$emit('item-selected', item)
			}
		},

		keyUp (e) {
			if (this.cursor > -1) {
				this.cursor--
				this.itemView(this.$el.getElementsByClassName('list-item')[this.cursor])
			}
		},

		keyDown (e) {
			if (this.cursor < this.internalItems.length) {
				this.cursor++
				this.itemView(this.$el.getElementsByClassName('list-item')[this.cursor])
			}
		},

		itemView (item) {
			if (item && item.scrollIntoView) {
				item.scrollIntoView(false)
			}
		},

		keyEnter () {
			if (this.showList && this.internalItems[this.cursor]) {
				this.onSelectItem(this.internalItems[this.cursor])
				this.showList = false
			}
		}
	},
	watch: {
		searchText: function (newValue) {
			this.innerSearchText = newValue
		}
	},
	created () {
		utils.minLen = this.minLen
		utils.wait = this.wait
		this.onSelectItem(this.selectedItem)
	}
}
</script>

<style scoped>
.list-item {
	background-color: #fff;
	padding: 10px;
	border-radius: 4px;
	border-bottom: 1px solid #409eff;
	border-left:1px solid #409eff;
	border-right:1px solid #409eff;
}

.list-item:last-child {
	border-bottom:none;
}

.list-item abbr {
	opacity:.8;
	font-size:.8em;
	display: block;
	font-family: sans-serif;
}

.list-item .list-item-active {
	background-color: #f3f6fa
}
</style>



