<template>
	<div style="display:flex;display:-webkit-flex;flex-direction:column;align-items:center;">
		<strong style="font-size:20px;width:auto;">众筹合约</strong>
		<hr style="width:75%;" size="1" color="#d9d9d9" />
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;margin-top:30px;">
			<strong>name</strong>
			<el-input id="crowdFundName" v-model="name" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>description</strong>
			<el-input id="crowdFundDescription" v-model="description" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong for="beginDate">openingTime</strong>
			<el-date-picker id="crowdFundOpeningTime" v-model="openingTime" type="date" placeholder="选择日期"></el-date-picker>
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong for="closeDate">closingTime</strong>
			<el-date-picker id="crowdFundClosingTime" v-model="closingTime" type="date" placeholder="选择日期"></el-date-picker>
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>rate</strong>
			<el-input id="crowdFundRate" v-model="rate" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>wallet</strong>
			<el-input id="crowdFundWallet" v-model="wallet" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>cap</strong>
			<el-input id="crowdFundCap" v-model="cap" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>goal</strong>
			<el-input id="crowdFundGoal" v-model="goal" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>receivedToken</strong>
			<el-input id="crowdFundReceivedToken" v-model="receivedToken" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>password</strong>
			<el-input id="crowdFundPassword" v-model="password" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>lockDuration</strong>
			<el-input id="crowdLockDuration" v-model="lockDuration" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;justify-content:flex-end;">
			<el-button id="crowdFundDeploy" @click="deploy" class="btn btn-primary">发布</el-button>
		</div>
	</div>
</template>

<script>
	import {getItem} from '@/localstorage/index'
	import localDB from '@/localstorage/localDB'
	import AutoCompleteContract from '@/components/auto_complete_contract/ContentItemList.vue';
	import '../utils/date.js';
	import { getBlockTime } from '@/contract.js';

	export default {
		name: 'CrowdFunding',
		data() {
			return {
				name: '',
				description: '',
				openingTime: new Date().format('yyyy-MM-dd'),
				closingTime: '',
				rate: 1,
				wallet: getItem('address'),
				cap: 0,
				goal: 0,
				receivedToken: '',
				password: '',
				lockDuration: 10,
			}
		},
		components: {
			AutoCompleteContract
		},
		methods: {
			deploy: function() {
				let self = this;
				self.axiosContractInstance.get('/newCrowdFunding', {
					params: {
						name: self.name,
						description: self.description,
						openingTime: parseInt(new Date(self.openingTime).getTime() / 1000),
						closingTime: parseInt(new Date(self.closingTime).getTime() / 1000),
						rate: self.rate,
						wallet: self.wallet,
						cap: self.cap,
						goal: self.goal,
						receivedToken: self.receivedToken,
						password: self.password,
						lockDuration: self.lockDuration,
						account: getItem('address')
					}
				}).then((response) => {
					if(response.data.code != 0) {
						console.log(response.data.msg)
						this.msg_error('发布失败', 'publish contract error')
						return
					}

					localDB.updateTokens(this,function (err) {
						if(err) {
							console.log(err)
							self.msg_warning('数据请求异常', '未获取到Tokens数据！')
						}
					})
					this.msg_success('发布成功', 'publish contract success')
				}).catch((err) => {
					console.log(err)
					this.msg_error('发布失败', '服务器未响应！')
				})
			}
		},
		watch: {
			name: function(newVal, oldVal) {
				let self = this
				if(self.symbol == oldVal) {
					self.symbol = newVal
				}
				if(self.description == oldVal) {
					self.description = newVal
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	strong {
		width: 300px;
		font-size: 15px;
	}
	
	.btn-primary {
		color: rgb(255, 255, 255);
		background-color: rgb(186, 27, 33);
		border-color: rgb(186, 27, 33);
	}
	
	.btn {
		margin-bottom: 8px;
		line-height: 32px;
		font-size: 14px;
		padding: 0px 20px;
		border-radius: 3px;
		display: inline-block;
		margin-bottom: 0px;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.42857;
		text-align: center;
		white-space: nowrap;
		vertical-align: middle;
		cursor: pointer;
		user-select: none;
		background-image: none;
		padding: 6px 12px;
		border-width: 1px;
		border-style: solid;
		border-color: transparent;
		border-image: initial;
		border-radius: 4px;
	}
</style>


