<template>
	<div>
		<search :datasOrigin="contractions" :keys="['name', 'description']" @searchTextChange="searchTextChange"></search>
		<hr style="width:100%;" size="1" color="#d9d9d9">
		<table class="table table-striped table-bordered table-hover">
			<thead style="background-color:#f9f9f9;">
				<tr>
					<th class="col-w-10 t-c">日期</th>
					<th class="col-w-25 t-c">发起人</th>
					<th class="col-w-15 t-c">标题</th>
					<th class="col-w-15 t-c">描述</th>
					<th class="col-w-25 t-c">address</th>
					<th class="col-w-10 t-c">查看详情</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="(contractShow, index) in contractsShow" :key="index">
					<td class="t-c" v-text="contractShow.date"></td>
					<td v-text="contractShow.publisher"></td>
					<td v-text="contractShow.name"></td>
					<td v-text="contractShow.description"></td>
					<td v-text="contractShow.address"></td>
					<td class="t-c">
						<el-button id='checkContractDetailButton' @click="checkContractDetail(contractShow)">detail</el-button>
					</td>
				</tr>
			</tbody>
		</table>
	</div>
</template>

<script>
	import Search from '@/components/search.vue'
	export default {
		name: 'ContractListPage',
		components: {
			Search
		},
		data() {
			return {
				searchText: "",
				contractions: [],
				contractsShow: []
			}
		},
		created: function() {
			let self = this

			self.axiosInstance.get('/contracts').then(function(res) {
				if(res.data.ret != 0) {
					console.log(res.data.msg)
					this.msg_error('未获取信息', 'get contracts error')
					return
				}
				self.contractions = [].concat(res.data.contractions)
				self.contractsShow = [].concat(res.data.contractions)
			}).catch(function(err) {
				this.msg_error('请求异常', '请求失败，服务器拒绝访问或未响应！')
			})
		},
		methods: {
			searchTextChange: function(datasFiltered) {
				let self = this
				self.contractsShow = datasFiltered
			},
			checkContractDetail: function(contractShow) {
				let self = this
				switch(contractShow.type) {
					case 'token':
						{
							self.$router.push({
								name: 'tokenDeployed',
								query: contractShow
							})
						}
						break;
					case 'crowdFunding':
						{
							self.$router.push({
								name: 'crowdFundingDeployed',
								query: contractShow
							})
						}
						break;
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.contracts {
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		width: 100%;
		font-size: 20px;
		padding: 12px;
		&:hover {
			background-color: rgb(220, 223, 230);
		}
		border-bottom-color:rgb(220, 223, 230);
		border-bottom-width:1px;
		border-bottom-style:solid;
		&:first-child {
			border-top-color: rgb(220, 223, 230);
			border-top-width: 1px;
			border-top-style: solid;
			border-bottom-color: rgb(220, 223, 230);
			border-bottom-width: 1px;
			border-bottom-style: solid;
		}
	}
	
	thead {
		display: table-header-group;
		vertical-align: middle;
		border-color: inherit;
	}
	
	.table-bordered {
		border: 1px solid #ddd;
	}
	
	.table {
		width: 100%;
		max-width: 100%;
		margin-bottom: 20px;
	}
	
	table.table {
		width: 100%;
		table-layout: fixed;
		background-color: transparent;
		border-collapse: collapse;
		border-spacing: 0;
		tr {
			td {
				word-wrap: break-word;
				height: 30px;
				vertical-align: middle;
			}
		}
	}
	
	.table-striped>tbody>tr:nth-of-type(odd) {
		background-color: #f9f9f9;
	}
	
	.col-w-10 {
		width: 10%;
	}
	
	.col-w-15 {
		width: 15%;
	}
	
	.col-w-20 {
		width: 20%;
	}
	
	.col-w-25 {
		width: 25%;
	}
	
	.t-c {
		text-align: center;
	}
</style>


