<template>
	<div>
		<search :datasOrigin="contractTemplates" :keys="keys" @searchTextChange="searchTextChange"></search>
		<hr style="width:100%;" size="1" color="#d9d9d9">
		<table class="table table-striped table-bordered table-hover">
			<thead style="background-color:#f9f9f9;">
				<tr>
					<th style="width:30%;">标题</th>
					<th style="width:70%;">描述</th>
				</tr>
			</thead>
			<tbody style="flex-grow:1;width:100%;overflow-y:auto;cursor:pointer;">
				<tr v-for="(value, index) in contractTemplatesShowed" :key="index" @click="() => {$router.push({name: value.route})}">
					<td :id="value.title" v-text="value.title"></td>
					<td v-text="value.description"></td>
				</tr>
			</tbody>
		</table>
	</div>
</template>

<script>
	import Search from '@/components/search.vue'

	export default {
		name: 'ContractTemplateListPage',
		components: {
			Search
		},
		data() {
			return {
				searchText: "",
				contractTemplates: [],
				contractTemplatesShowed: [],
				keys: ['title', 'description']
			}
		},
		created() {
			let self = this
			self.contractTemplatesShowed = [{
					"title": "crowdFunding",
					"description": "crowdFunding",
					"route": "crowdFunding"
				},
				{
					"title": "token",
					"description": "token",
					"route": "token"
				}
			]
		},
		methods: {
			searchTextChange: function(datasFiltered) {
				let self = this
				self.contractTemplatesShowed = datasFiltered
			}
		}
	}
</script>

<style lang="scss" scoped>
	.contractTemplate {
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		width: 100%;
		font-size: 20px;
		padding: 12px;
		cursor: pointer;
		&:hover {
			background-color: rgb(220, 223, 230);
		}
		border-bottom-color:rgb(220, 223, 230);
		border-bottom-width:1px;
		border-bottom-style:solid;
		&:first-child {
			border-top-color: rgb(220, 223, 230);
			border-top-width: 1px;
			border-top-style: solid;
			border-bottom-color: rgb(220, 223, 230);
			border-bottom-width: 1px;
			border-bottom-style: solid;
		}
	}
	
	thead {
		display: table-header-group;
		vertical-align: middle;
		border-color: inherit;
	}
	
	.table-bordered {
		border: 1px solid #ddd;
	}
	
	.table {
		width: 100%;
		max-width: 100%;
		margin-bottom: 20px;
	}
	
	table {
		width: 100%;
		table-layout: fixed;
		background-color: transparent;
		border-collapse: collapse;
		border-spacing: 0;
	}
	
	.table-striped>tbody>tr:nth-of-type(odd) {
		background-color: #f9f9f9;
	}
</style>


