<template>
	<div>
		<strong style="font-size:30px;">交易记录</strong>
		<div class="ibox-content">
			<table class="table table-striped table-bordered table-hover">
				<thead style="background-color:#f9f9f9; text-align: center;">
					<tr>
						<th style="width:20%;">日期</th>
						<th style="width:15%;">token name</th>
						<th style="width:35%;">token address</th>
						<th style="width:35%;">发件人</th>
						<th style="width:35%;">接收方</th>
						<th style="width:20%;">数量</th>
					</tr>
				</thead>
				<tbody style="height:100%;width:100%;overflow-y:auto;">
					<tr v-for="(transaction, index) in transactions" :key="index">
						<td v-text="transaction.date"></td>
						<td v-text="transaction.name"></td>
						<td v-text="transaction.address"></td>
						<td v-text="transaction.sender"></td>
						<td v-text="transaction.recepient"></td>
						<td v-text="transaction.ammount"></td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</template>
<script>
	export default {
		name: 'homePage',
		data() {
			return {
				account: 'test',
				balance: 0,
				transactions: []
			}
		},
		created() {
			let self = this
			self.axiosInstance.get('/transactions').then(response => {
				if(response.data.ret != 0) {
					console.log(response.data.msg)
					this.msg_error('请求异常', 'get transactions error')
					return
				}
				self.transactions = response.data.transactions
			}).catch(err => {
				this.msg_error('请求异常', '访问数据库失败')
			})
		}
	}
</script>

<style lang="scss" scoped type="">
	.transactions {
		display: flex;
		display: -webkit-flex;
		flex-direction: column;
		align-items: flex-start;
		text-align: left;
		width: 100%;
		height: -moz-calc(100% - 60px);
		height: -webkit-calc(100% - 60px);
		height: calc(100% - 60px);
		top: 60px;
	}
	
	.ibox-content {
		clear: both;
		margin-bottom: 0;
		padding: 15px 20px 20px;
		min-height: 48px;
		color: inherit;
		background-color: #fff;
		border-color: #e7eaec;
		-webkit-border-image: none;
		-o-border-image: none;
		border-image: none;
		border-style: solid solid none;
		border-width: 1px 0;
	}
	
	thead {
		display: table-header-group;
		vertical-align: middle;
		border-color: inherit;
	}
	
	.table-bordered {
		border: 1px solid #ddd;
	}
	
	.table {
		width: 100%;
		max-width: 100%;
		margin-bottom: 20px;
	}
	
	table {
		width: 100%;
		table-layout: fixed;
		background-color: transparent;
		border-collapse: collapse;
		border-spacing: 0;
	}
	
	.table-striped>tbody>tr:nth-of-type(odd) {
		background-color: #f9f9f9;
	}
</style>


