<template>
    <div style="display:flex;display:-webkit-flex;align-items:center;height:50px;">
        <input class="local-search-input" type="text" v-model="searchText" placeholder="请输入搜索内容"/>
    </div>
</template>

<script>

export default {
    name: 'search',
    props: {
        keys: [],
        datasOrigin: []
    },
    data () {
        return {
            searchText: '',
            datasFiltered: []
        }
    },
    watch: {
        searchText: function(newVal) {
        	console.log(keys, datasOrigin, newVal)
            let self = this
            if (newVal.length < 2) {
                self.datasFiltered = [].concat(self.datasOrigin)
                self.$emit('searchTextChange', self.datasFiltered)
                return
            }

            let searchText = newVal.trim().toLowerCase()

            self.datasFiltered = []
            self.datasOrigin.forEach(function(dataOrigin) {
                for (let key of self.keys) {
                    if(dataOrigin[key].indexOf(self.searchText) >= 0) {
                        self.datasFiltered.push(dataOrigin)
                        break
                    }
                }
            })
            self.$emit('searchTextChange', self.datasFiltered)
        }
    }
}
</script>

<style lang="scss" type="text/css" scoped>
.local-search-input {
    width: 100%;
    margin:10px;
    height: 40px;
    border: 1px solid #EEEEEE;
    font-size: 16px;
    line-height: 40px;
    padding: 10px;
    background:  #F9F9F9;
    color: #000000;
    transition: 0.3s;
    border-radius: 5px;
    outline: none;
    &:focus{
        border: #2C3E50 1px solid;
    }
}
</style>



