<template>
	<div @keyup.tab="sidebarShow = !sidebarShow,ContentWz = !ContentWz">
		<div id="sidebar" style="display: block;" :class="{'sidebar-is-show': sidebarShow}" >
			<div id="sidebar-content" style="float: left;">
				<router-link class="logo border-bottom sidebar-font-style" :to="{name: 'homePage'}">GcBc</router-link>
				<label class="sidebar-username border-bottom sidebar-font-style">{{ username }}</label>
				<div class="addr border-bottom">
					<label class="sidebar-font-style">地址<i @click="copy" class="el-icon-document"></i></label>
					<div>
						<span id="attr-id">{{address}}</span>
					</div>
				</div>
				<div class="BcName border-bottom">
					<label class="sidebar-font-style">代币名称</label><br />
					<label class="bc-name">{{tokenName}}</label><br />
					<el-button @click="tokenListShow = true" class="bcname-select-btn">选择代币<i class="el-icon-arrow-right"></i></el-button>
				</div>
				<div class="token-addr border-bottom">
					<label class="sidebar-font-style">tokenAddress</label>
					<div>
						<span id="token-id">{{tokenAddress}}</span>
					</div>
				</div>
				<div class="zichan border-bottom">
					<label class="sidebar-font-style">拥有资产</label><br />
					<span>{{tokenBalance}}</span>
				</div>
				<div class="gas-ammount border-bottom">
					<label class="sidebar-font-style">gas ammount</label><br />
					<span style="margin-top:10px;">{{gasAmmount}}</span>
				</div>
				<div class="button-group">
					<el-button id="transferGasShowButton"class="btn" @click="transferGasShow = true">send gas</el-button>
					<el-button id="transferTokenShowButton" class="btn" @click="transferShow = true">发送</el-button>
					<el-button id='contractListButton' class="btn" @click="$router.push({name:'contractListPage'})">合约</el-button>
					<el-button id='contractTemplateListButton' class="btn" @click="$router.push({name:'contractTemplateListPage'})">合约模板</el-button>
					<el-button class="btn" @click="quit">退出</el-button>
				</div>
			</div>
			<div style="width: 5px; height: 100%; float: left;" >
				<div class="triangle" style="cursor: pointer;" @click="sidebarShow = !sidebarShow,ContentWz = !ContentWz"></div>
			</div>
		</div>
		<div id="routes" :class="{'content-wz': ContentWz}">
			<div style="padding:20px;">
				<router-view></router-view>
			</div>
		</div>
		<el-dialog title="选择代币" :visible.sync="tokenListShow" width="30%" :show-close="false" :center="true">
			<el-select class="el-select" v-model="tokenName" @change="changeToken">
				<el-option v-for="token in tokenList" :key="token.address" :value="token.name" />
			</el-select>
		</el-dialog>
		<el-dialog title="发送国脉币" :visible.sync="transferShow" width="40%" :show-close="false" :center="true" id="transfer" class="dialog">
			<div class="main">
				<div class="lside">tokenAddress</div>
				<div class="rside">
					<el-input id="transferTokenTokenAddress" v-model="tokenAddress"></el-input>
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">接收方</div>
				<div class="rside">
					<vue-instant :suggestion-attribute="suggestionAttribute" id="transferTokenRecepient" v-model="recepient" :disabled="false" :show-autocomplete="true" :autofocus="false" :suggestions="suggestions" name="customName" placeholder="custom placeholder" type="google"></vue-instant>
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">数量</div>
				<div class="rside">
					<el-input id="transferTokenAmmount" v-model="ammount"></el-input>
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">gas</div>
				<div class="rside">
					<el-input id="transferTokenGas" v-model="gas"></el-input>
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">支付密码</div>
				<div class="rside">
					<el-input type="password" id="transferTokenPassword" v-model="password" />
				</div>
			</div>
			<div class="footer">
				<el-button id="transferTokenButton" @click="transfer" class="btn btn-primary" style="margin-right: 20px;">确认</el-button>
				<el-button id="transferTokenCancelButton" @click="() => {recepient='';ammount=0;transferShow=false;}" class="btn btn-sm btn-primary btn-search">取消</el-button>
			</div>
			<div class="line" style="height: 1px;"></div>
		</el-dialog>
		<el-dialog title="transfer gas" :visible.sync="transferGasShow" width="40%" :show-close="false" :center="true" id="transferGas" class="dialog">
			<div class="main">
				<div class="lside">接收方</div>
				<div class="rside">
					<vue-instant :suggestion-attribute="suggestionAttribute" id="transferGasRecepient" v-model="recepient" :disabled="false" :show-autocomplete="true" :autofocus="false" :suggestions="suggestions" name="customName" :placeholder="'custom placeholder'" :type="'google'"></vue-instant>
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">数量</div>
				<div class="rside">
					<el-input id="transferGasAmmount" v-model="ammount" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">gas</div>
				<div class="rside">
					<el-input id="transferGasGas" v-model="gas"></el-input>
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">支付密码</div>
				<div class="rside">
					<el-input id="transferGasPassword" type="password" v-model="password" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">unlockDurationSec</div>
				<div class="rside">
					<el-input type="text" v-model="unlockDurationSec" />
				</div>
			</div>
			<div class="footer">
				<el-button id="transferGasButton" @click="transferGas" class="btn btn-primary" style="margin-right: 20px;">确认</el-button>
				<el-button id="transferGasCancelButton" @click="() => {recepient='';ammount=0;transferGasShow=false;}" class="btn btn-sm btn-primary btn-search">取消</el-button>
			</div>
			<div class="line" style="height: 1px;"></div>
		</el-dialog>
	</div>
</template>
<script>
	import { unlockAccount, transferGas, getArtifact, gmToken } from './contract'
	import QS from 'qs'
	import SearchInput from './components/search'
	import VueInstant from './components/VueInstant'
	import { setItem, getItem, delAllItem } from './localstorage/index'
	import localDB from '@/localstorage/localDB'

	export default {
		name: 'index',
		data() {
			return {
				username: getItem('username'),
				address: getItem('address'),
				tokenName: getItem('tokenName'),
				tokenAddress: getItem('tokenAddress'),
				tokenBalance: getItem('tokenBalance'),
				gasAmmount: getItem('gesAmmount'),
				tokenList: JSON.parse(getItem('tokenList')),

				transferGasShow: false,
				transferShow: false,
				recepient: '',
				ammount: 0,
				password: '',
				gas: 999999,
				unlockDurationSec: 10,
				tokenListShow: false,
				selected: null,
				sidebarShow: true,
				ContentWz: false,
				suggestionAttribute: 'name',
				suggestions: [{"key": '0x001',"name": 'aaaaaaa'},{"key": '0x002',"name": 'bbbbbbb'},{"key": '0x003',"name": 'ccccccc'},{"key": '0x004',"name": "boyyyyy"}]
			}
		},
		components: {
			SearchInput,
			VueInstant
		},
		created () {
			this.username = getItem('username')
			this.address = getItem('address')
			this.tokenName = getItem('tokenName')
			this.tokenAddress = getItem('tokenAddress')
			this.tokenBalance = getItem('tokenBalance')
			this.gasAmmount = getItem('gesAmmount')
			this.tokenList = JSON.parse(getItem('tokenList'))

			let addr = this.address
			if (addr) {
				return
			}else{
				self.$router.push({'name':'LoginPage'})
			}
		},
		watch: {
			username (newVal) {setItem('username',newVal)},
			address (newVal) {setItem('address',newVal)},
			tokenName (newVal) {setItem('tokenName',newVal)},
			tokenAddress (newVal) {setItem('tokenAddress',newVal)},
			tokenBalance (newVal) {setItem('tokenBalance',newVal)},
			gasAmmount (newVal) {setItem('gasAmmount',newVal)},
			tokenList (newVal) {setItem('tokenList',newVal)}
		},
		mounted () {
			let self = this;
			localDB.updateTokens(this,function (err) {
				if(err) {
					self.msg_warning('updateTokens error', err)
				}
			})
			localDB.updateGas(self.address)
		},
		methods: {
			copy() {},
			changeToken(tokenName) {
				let self = this;
				let token = null

				for(let tokenIte of this.tokenList) {
					if(tokenIte.name == tokenName) {
						token = tokenIte
						break
					}
				}
				if(token == null) {
					return
				}
				setItem('tokenAddress', token.address)
				self.tokenListShow = false;
				getArtifact(self.axiosInstance, token.address, (err, artifact) => {
					if(err) {
						
						self.msg_warning('getArtifact error', err)
						return
					}
					gmToken.balanceOf(this.address, artifact, this.address, function(err, balance) {
						if(err) {
							
							self.msg_error('balanceOf error', err)
							return
						}
						self.msg_success('balance success', balance.toNumber())
						this.tokenBalance = parseInt(balance)
					})
				})
			},
			transferGas() {
				let self = this

				transferGas(self.address, self.password, self.unlockDurationSec, self.recepient, self.ammount, self.gas, function(err) {
					if(err) {
						
						self.msg_error('transferGas error', err)
						return
					}
					self.axiosInstance.post("/saveTransaction", QS.stringify({
						sender: self.address,
						recepient: self.recepient,
						ammount: self.ammount,
						name: 'ethereum',
						address: ''
					}, {
						allowDots: true
					})).then(response => {
						if(response.data.ret != 0) {
							self.msg_error('save transaction error', response.data.msg)
							return
						} else {
							self.msg_success('save transaction success', '')
						}
					}).catch(err => {
						
						self.msg_error('save transaction error', err)
					})
				})
			},
			transfer() {
				let self = this    
				new Promise((resolve, reject) => {
					getArtifact(self.axiosInstance, self.tokenAddress, (err, artifact) => {
						if(err) {
							reject('get token artifact error: ' + err)
							return
						}
						resolve(artifact)
					})
				}).then(artifact => {
					return new Promise((resolve, reject) => {
						gmToken.transfer(self.address, self.password, self.unlockDurationSec, artifact, self.recepient, self.ammount, self.gas, function(err, result) {
							if(err) {
								reject('transfer error: ' + err)
								return
							}
							resolve()
						})
					})
				}).then(() => {
					self.axiosInstance.post("/saveTransaction", QS.stringify({
						sender: self.address,
						recepient: self.recepient,
						ammount: self.ammount,
						name: self.tokenName,
						address: self.tokenAddress
					}, {
						allowDots: true
					})).then(response => {
						if(response.data.ret != 0) {
							self.msg_error('transaction save error', response.data.msg)
							return
						} else {
							self.msg_success('transaction save success', '')
						}
					}).catch(err => {
						self.msg_error('transaction save error', err)
					})
				}).catch(err => {
					self.msg_error('transfer token error', err)
					self.recepient = '';
					self.ammount = 0;
					self.password = '';
					self.unlockDurationSec = 10;
				})
			},
			quit() {
				delAllItem();
				this.$router.push('/');
			},
			sidebarChange() {
				this.sidebarShow = !this.sidebarShow
				this.ContentWz = !this.ContentWz
			}
		}
	}
</script>

<style lang="scss" scoped>
	#sidebar {
		* {
			margin: 0;
			padding: 0;
		}
		position: fixed;
		display: flex;
		flex-direction: column;
		display: -webkit-flex;
		align-items: center;
		width: 205px;
		height: 100%;
		top: 0px;
		z-index: 3000;
		
		color: #FFFFFF;
		transition: 1s;
		.triangle {
			position: absolute;
			width: 0;
			height: 0;
			border-width: 20px 0px 20px 20px;
			border-style: solid;
			border-color: transparent #2c3e50 transparent;
			top: 50%;
			margin-top:-20px;
			position: relative;
		}
		.logo {
			width: 100%;
			height: 60px;
			line-height: 60px;
			display: block;
			text-decoration: none;
			font-weight: bold;
			transition: 0.5s;
			&:hover {
				font-size: 22px;
			}
		}
		#sidebar-content {
			background-color: #2c3e50;
			display: flex;
			display: -webkit-flex;
			flex-direction: column;
			align-items: center;
			flex-grow: 1;
			width: 200px;
			overflow-y: scroll;
			height: 100vh;
			.sidebar-username {
				width: 90%;
				padding-top: 20px;
				padding-bottom: 20px;
			}
			.addr {
				width: 90%;
				text-align: center;
				padding: 20px 0px;
				label {
					padding-bottom: 10px;
					i {
						cursor: pointer;
					}
				}
				div {
					overflow: hidden;
					overflow-x: auto;
					/*padding: 0px 10px;*/
					padding-bottom: 10px;
					span {
						text-align: center;
						overflow-x: scroll;
					}
				}
			}
			.BcName {
				width: 100%;
				text-align: center;
				padding: 20px 0px;
				.bc-name {
					font-size: 16px;
					margin: 10px 0px;
				}
				.bcname-select-btn {
					padding: 10px 20px;
				}
			}
			.token-addr {
				width: 90%;
				text-align: center;
				padding: 20px 0px;
				div {
					overflow: hidden;
					overflow-x: auto;
					/*padding: 0px 10px;*/
					padding-bottom: 10px;
					span {
						text-align: center;
						overflow-x: scroll;
					}
				}
			}
			.zichan {
				width: 90%;
				text-align: center;
				padding: 10px 0px;
				label {
					padding-bottom: 10px;
				}
				span {
					font-weight: bold;
					font-size: 16px;
				}
			}
			.button-group {
				width: 90%;
				text-align: center;
				.btn {
					padding: 10px 0px;
					margin-top: 15px;
				}
			}
			.gas-ammount {
				width: 90%;
				text-align: center;
				padding: 10px 0px;
			}
		}
		.border-bottom {
			border-bottom: 1px solid #FFFFFF;
		}
		.sidebar-font-style {
			font-size: 20px;
			text-align: center;
			color: #FFFFFF;
		}
	}
	
	#routes {
		transition: 1s;
		margin-left: 20px;
	}
	
	#routes.content-wz {
		margin-left: 220px;
	}
	
	body input {
		background-color: blue;
	}
	
	select {
		border-width: 0;
		appearance: none;
		-moz-appearance: none;
		-webkit-appearance: none;
	}
	
	.dialog {
		clear: both;
		.main {
			.lside {
				width: 25%;
				float: left;
				line-height: 40px;
				font-size: 16px;
				font-weight: bold;
				text-align: center;
			}
			.rside {
				width: 75%;
				float: left;
			}
		}
		.line {
			height: 10px;
			width: 100%;
			clear: both;
		}
		.footer {
			margin-top: 10px;
			float: right;
		}
	}
	
	.tag {
		padding: 20px;
		cursor: pointer;
		&:hover {
			text-decoration: underline;
		}
	}
	
	.btn-primary {
		color: rgb(255, 255, 255);
		background-color: rgb(186, 27, 33);
		border-color: rgb(186, 27, 33);
	}
	
	.btn {
		width: 100px;
		margin: 10px 0px 0px 0px;
	}
	
	.v-modal {
		z-index: 900;
	}
	
	.el-select {
		width: 100%;
		max-width: 506px;
		text-align: center;
		margin: 20px auto;
		display: block;
	}
	
	.sidebar-is-show {
		transform: translateX(-200px);
	}
</style>


