<template>
	<div style="display:flex;display:-webkit-flex;flex-direction:column;align-items:center;">
		<strong style="font-size:20px;width:auto;">代币</strong>
		<hr style="width:75%;" size="1" color="#d9d9d9" />
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;margin-top:30px;">
			<strong>名称</strong>
			<el-input id='tokenName' v-model="name" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>标记</strong>
			<el-input id='tokenSymbol' v-model="symbol" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>小数</strong>
			<el-input id='tokenDecimals' v-model="decimals" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>description</strong>
			<el-input id='tokenDescription' v-model="description" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>password</strong>
			<el-input id='tokenPassword' v-model="password" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;">
			<strong>lockDuration</strong>
			<el-input id='tokenLockDuration' v-model="lockDuration" />
		</div>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;justify-content:flex-end;">
			<el-button id='tokeDeploy' @click="deploy" class="btn btn-primary">发布</el-button>
		</div>
	</div>
</template>

<script>
	import {getItem} from '@/localstorage/index'
	import localDB from '@/localstorage/localDB'

	export default {
		name: 'CrowdFunding',
		data() {
			return {
				name: '',
				symbol: '',
				decimals: 18,
				description: '',
				password: '',
				lockDuration: 10
			}
		},
		methods: {
			deploy: function() {
				let self = this;
				self.axiosContractInstance.get('/newToken', {
					params: {
						name: self.name,
						symbol: self.symbol,
						decimals: self.decimals,
						description: self.description,
						account: getItem('address'),
						password: self.password,
						lockDuration: self.lockDuration
					}
				}).then((response) => {
					if(response.data.code != 0) {
						this.msg_error('发布失败', response.data.msg)
						return
					}
					localDB.updateTokens(this,function (err) {
						if(err) {
							console.log(err)
							self.msg_warning('数据请求异常', '未获取到Tokens数据！')
						}
					})
					this.msg_success('发布成功', 'publish contract success')
				}).catch((err) => {
					console.log(err)
					this.msg_error('发布失败', 'catch, publish contract error')
				})
			}
		},
		watch: {
			name: function(newVal, oldVal) {
				let self = this
				if(self.symbol == oldVal) {
					self.symbol = newVal
				}
				if(self.description == oldVal) {
					self.description = newVal
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	strong {
		width: 300px;
		font-size: 15px;
	}
	
	.btn-primary {
		color: rgb(255, 255, 255);
		background-color: rgb(186, 27, 33);
		border-color: rgb(186, 27, 33);
	}
	
	.btn {
		margin-bottom: 8px;
		line-height: 32px;
		font-size: 14px;
		padding: 0px 20px;
		border-radius: 3px;
		display: inline-block;
		margin-bottom: 0px;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.42857;
		text-align: center;
		white-space: nowrap;
		vertical-align: middle;
		cursor: pointer;
		user-select: none;
		background-image: none;
		padding: 6px 12px;
		border-width: 1px;
		border-style: solid;
		border-color: transparent;
		border-image: initial;
		border-radius: 4px;
	}
</style>


