import { setItem, delItem, getItem } from '../localstorage'
import { unlockAccount, gasAmmount, getArtifact, gmToken } from '../contract'

let localDB = {
	setAddress (newValue) {
		setItem('address',parseInt(newValue, 16))
	},
	updateTokens(component, cb) {
		component.axiosInstance.get('/tokens').then(function(response) {
			if(response.data.ret != 0) {
				cb(response.data.msg);
				return
			}
			setItem('tokenList', response.data.tokens);
			let tokenList = JSON.parse(getItem('tokenList'));
			let tokenName = tokenList[0] ? tokenList[0].name : ''
			setItem('tokenName', tokenName);
			let tokenAddress = tokenList[0] ? tokenList[0].address : ''
			setItem('tokenAddress', tokenAddress)

			if(getItem('tokenAddress') == '') {
				cb(null)
				return
			}

			getArtifact(component.axiosInstance, tokenList[0].address, (err, artifact, cb) => {
				if(err) {
					cb('get token artifact error: ' + err)
					return
				}
				let address = getItem('address')
				gmToken.balanceOf(address, artifact, address, function(err, balance, cb) {
					if(err) {
						cb('get balance error: ' + err)
						return
					}
					component.$notify({
						title: 'get balance',
						message: balance.toNumber()
					});
					setItem('tokenBalance',parseInt(balance))
				})
			})
		}).catch((err) => {
			console.log(err)
		});
	},
	updateGas(target) {
		console.log("updateGas target: ",target)
		setItem('gasAmmount',gasAmmount(target))
	}
}

export default localDB


