<template>
	<div>
		<v-autocomplete
			:searchText="value"
			:items="innerItems"
			:get-label='getLabel'
			:min-len='0'
			:componentItem="contentItemTemplate"
			:input-attrs="{placeholder: language.descriptionInputPlaceholder}"
			@blur="() => { this.$emit('blur') }"
			@input-change="inputChange"
			@update-items="updateItems"
			@item-selected="itemSelected">
		</v-autocomplete>
	</div>
</template>

<script>
import Autocomplete from '../auto_complete/Autocomplete'
import ContentItemTemplate from './ContentItemTemplate'
export default {
	name: 'content-item-list',
	components: {
		'v-autocomplete': Autocomplete
	},
	props: {
		value: '',
		items: {
			type: Array,
			default () {
				return []
			}
		}
	},
	data () {
		return {
			contentItemTemplate: ContentItemTemplate,
			innerItems: Object.assign([], this.items) || []
		}
	},
	methods: {
		itemSelected (item) {
			this.$emit('input', this.getLabel(item))
			this.$emit('item-selected', item)
		},
		getLabel (item) {
			if (item) {
				return item.description
			}
			return ''
		},
		inputChange (text) {
			this.$emit('input', text)
		},
		updateItems (text) {
			this.filterItems(text, this.items)
		},
		filterItems: function (text, items) {
			this.innerItems = items.filter((item) => {
				return (new RegExp(text.toLowerCase())).test(item.name.toLowerCase())
			})
		}
	}
}
</script>

<style scoped>
</style>



