<template>
	<div style="text-align:left;">
		<b>{{item.name}}</b>
		<br>
		<span>{{ item.description }}</span>
		<br>
		<span>{{ item.address }}</span>
	</div>
</template>

<script>
export default {
	name: 'content-item-template',
	props: {
		item: { required: true }
	}
}
</script>

<style scoped>
</style>



