<template>
	<div id="CrowdFundingDeployed">
		<strong style="font-size:20px;width:auto;">众筹合约</strong>
		<hr style="width:75%;" size="1" color="#d9d9d9" />
		<div class="CrowdFundingItems">
			<strong>name</strong><span v-text="contract.name"></span>
		</div>
		<div class="CrowdFundingItems">
			<strong>description</strong><span v-text="contract.description"></span>
		</div>
		<div class="CrowdFundingItems">
			<strong>balance</strong><span v-text="contract.balance"></span>
		</div>
		<div class="CrowdFundingItems">
			<strong>receivedTokenRaised</strong><span v-text="contract.receivedTokenRaised"></span>
		</div>
		<div class="CrowdFundingItems">
			<strong>openingTime</strong><span v-text="contract.openingTime"></span>
		</div>
		<div class="CrowdFundingItems">
			<strong>closingTime</strong><span v-text="contract.closingTime"></span>
		</div>
		<div class="CrowdFundingItems">
			<strong>rate</strong><span v-text="contract.rate"></span>
		</div>
		<div class="CrowdFundingItems">
			<strong>wallet</strong><span v-text="contract.wallet"></span>
		</div>
		<div class="CrowdFundingItems">
			<strong>cap</strong><span v-text="contract.cap"></span>
		</div>
		<div class="CrowdFundingItems">
			<strong>goal</strong><span v-text="contract.goal"></span>
		</div>
		<div class="CrowdFundingItems">
			<strong>receivedToken</strong>
			<el-input v-model="contract.receivedToken" />
		</div>
		<div class="CrowdFundingItems">
			<strong>distributedToken</strong>
			<el-input v-model="contract.distributedToken" />
		</div>
		<strong style="font-size:20px; text-align: center; line-height: 50px;">股东</strong>
		<table style="width:75%" class="table table-striped table-bordered table-hover">
			<thead style="background-color: #f9f9f9;">
				<tr>
					<th style="width:30%;">合约地址</th>
					<th style="width:70%;">代币持有</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="(shareholder, index) in Object.keys(contract.shareholders)" :key="index">
					<td style="width:30%;">{{shareholder}}</td>
					<td style="width:70%;">{{contract.shareholders[shareholder]}}</td>
				</tr>
			</tbody>
		</table>
		<div style="width:75%;display:flex;display:-webkit-flex;align-items:center;height:50px;justify-content:flex-end;">
			<el-button id="buyTokensShowButton" @click="buyTokenShow=true" class="btn btn-primary">buyTokens</el-button>
			<el-button @click="finalizeShow=true" class="btn btn-primary">finalize</el-button>
			<el-button @click="claimRefundShow=true" class="btn btn-primary">claimRefund</el-button>
		</div>
		<el-dialog title="投资" :visible.sync="buyTokenShow" width="50%" :shou-close="false" :center="true" class="dialog">
			<div class="main">
				<div class="lside">beneficiary</div>
				<div class="rside">
					<el-input id="buyTokensBeneficiary" v-model="beneficiary" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">投资金额</div>
				<div class="rside">
					<el-input id="buyTokensAmmount" v-model="ammount" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">gas</div>
				<div class="rside">
					<el-input id="buyTokensGas" v-model="gas" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">密码</div>
				<div class="rside">
					<el-input id="buyTokensPassword" v-model="password" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">unlockDurationSec</div>
				<div class="rside">
					<el-input v-model="unlockDurationSec" />
				</div>
			</div>
			<div class="footer">
				<el-button id="buyTokensButton" @click="buyTokens" class="btn btn-primary">确认</el-button>
				<el-button id="buyTokensCancelButton" @click="() => {ammount=0;password='';unlockDurationSec=10;buyTokenShow=false;}" class="btn btn-sm btn-primary btn-search">取消</el-button>
			</div>
			<div class="line" style="height: 1px;"></div>
		</el-dialog>
		<el-dialog title="finalize" :visible.sync="finalizeShow" width="50%" :shou-close="false" :center="true" class="dialog">
			<div class="main">
				<div class="lside">gas</div>
				<div class="rside">
					<el-input v-model="gas" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">密码</div>
				<div class="rside">
					<el-input v-model="password" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">unlockDurationSec</div>
				<div class="rside">
					<el-input v-model="unlockDurationSec" />
				</div>
			</div>
			<div class="footer">
				<el-button @click="finalize" class="btn btn-primary">确认</el-button>
				<el-button @click="() => {password='';unlockDurationSec=10;finalizeShow=false;}" class="btn btn-sm btn-primary btn-search">取消</el-button>
			</div>
			<div class="line" style="height: 1px;"></div>
		</el-dialog>
		<el-dialog title="claimRefund" :visible.sync="claimRefundShow" width="50%" :shou-close="false" :center="true" class="dialog">
			<div class="main">
				<div class="lside">gas</div>
				<div class="rside">
					<el-input v-model="gas" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">密码</div>
				<div class="rside">
					<el-input v-model="password" />
				</div>
			</div>
			<div class="line"></div>
			<div class="main">
				<div class="lside">unlockDurationSec</div>
				<div class="rside">
					<el-input v-model="unlockDurationSec" />
				</div>
			</div>
			<div class="footer">
				<el-button @click="claimRefund" class="btn btn-primary">确认</el-button>
				<el-button @click="() => {password='';unlockDurationSec=10;claimRefundShow=false;}" class="btn btn-sm btn-primary btn-search">取消</el-button>
			</div>
			<div class="line" style="height: 1px;"></div>
		</el-dialog>
	</div>
</template>

<script>
import {gmCrowdFunding, gmToken, getArtifact} from '../contract'
import '../utils/date.js'
import {getItem} from '@/localstorage/index'
export default {
  name: 'CrowdFundingDeployed',
  data () {
    return {
      buyTokenShow: false,
      claimRefundShow: false,
      finalizeShow: false,
      beneficiary: getItem('address'),
      ammount: 0,
      password: '',
      unlockDurationSec: 10,
      gas: 999999,
      contract: {
        name: '',
        description: '',
        openingTime: 0,
        closingTime: 0,
        rate: 1,
        wallet: '',
        cap: 0,
        goal: 0,
        receivedToken: '',
        distributedToken: '',
        receivedTokenRaised: 0,
        shareholders: {}
      }
    }
  },
  created: function () {
    let self = this

	$.extend(self.contract, self.$route.query)
	getArtifact(self.axiosInstance, self.contract.address, (err, artifact) => {
		if(err) {
			
			self.msg_error('getArtifact error', err)
			return
		}

		gmCrowdFunding.watch(self, artifact, function() {
				})

		gmCrowdFunding.balanceOf(getItem('address'), artifact, getItem('address'), function(err, balance) {
			if(err) {
				
				self.msg_error('balanceOf error', err)
				return
			}
			self.contract.balance = parseInt(balance)
		})

			gmCrowdFunding.openingTime(getItem('address'), artifact, function(err, openingTime) {
				if(err) {
					
					self.msg_error('openingTime error', err)
					return
				}
				self.contract.openingTime = new Date(parseInt(openingTime)).format("yyyy-MM-dd")
			})

			gmCrowdFunding.closingTime(getItem('address'), artifact, function(err, closingTime) {
				if(err) {
					
					self.msg_error('closingTime error', err)
					return
				}
				self.contract.closingTime = new Date(parseInt(closingTime)).format("yyyy-MM-dd")
			})

			gmCrowdFunding.rate(getItem('address'), artifact, function(err, rate) {
				if(err) {
					
					self.msg_error('rate error', err)
					return
				}
				self.contract.rate = parseInt(rate)
			})
			gmCrowdFunding.wallet(getItem('address'), artifact, function(err, wallet) {
				if(err) {
					
					self.msg_error('wallet error', err)
					return
				}
				self.contract.wallet = wallet
			})

			gmCrowdFunding.cap(getItem('address'), artifact, function(err, cap) {
				if(err) {
					
					self.msg_error('cap error', err)
					return
				}
				self.contract.cap = parseInt(cap)
			})

			gmCrowdFunding.goal(getItem('address'), artifact, function(err, goal) {
				if(err) {
					
					self.msg_error('goal error', err)
					return
				}
				self.contract.goal = parseInt(goal)
			})

			gmCrowdFunding.receivedToken(getItem('address'), artifact, function(err, receivedToken) {
				if(err) {
					
					self.msg_error('receivedToken error', err)
					return
				}
				self.contract.receivedToken = receivedToken
			})

			gmCrowdFunding.distributedToken(getItem('address'), artifact, function(err, distributedToken) {
				if(err) {
					
					self.msg_error('distributedToken error', err)
					return
				}
				self.contract.distributedToken = distributedToken
			})

			gmCrowdFunding.receivedTokenRaised(getItem('address'), artifact, function(err, receivedTokenRaised) {
				if(err) {
					
					self.msg_error('receivedTokenRaised error', err)
					return
				}
				self.contract.receivedTokenRaised = parseInt(receivedTokenRaised)
			})
		})
	},
	methods: {
		buyTokens: function() {
			let self = this
			getArtifact(self.axiosInstance, self.contract.address, (err, artifact) => {
				if(err) {
					self.msg_error('getArtifact error', err)
					return
				}
				gmCrowdFunding.buyTokens(getItem('address'), self.password, self.unlockDurationSec, artifact, self.beneficiary, self.ammount, self.gas, function(err, distributedToken) {
					if(err) {
						self.msg_error('buyTokens error', err)
						return
					}
				})
			})
		},
		claimRefund: function() {
			let self = this
			getArtifact(self.axiosInstance, self.contract.address, (err, artifact) => {
				if(err) {
					self.msg_error('getArtifact error', err)
					return
				}
				gmCrowdFunding.claimRefund(getItem('address'), self.password, self.unlockDurationSec, artifact, self.gas, function(err, distributedToken) {
					if(err) {
						self.msg_error('claimRefund error', err)
						return
					}
					self.msg_success('claimRefund successed', '')
				})
			})
		},
		finalize: function() {
			let self = this
			getArtifact(self.axiosInstance, self.contract.address, (err, artifact) => {
				if(err) {
					self.msg_error('getArtifact error', err)
					return
				}
				gmCrowdFunding.finalize(getItem('address'), self.password, self.unlockDurationSec, artifact, self.gas, function(err, distributedToken) {
					if(err) {
						self.msg_error('finalize error', err)
						return
					}
					self.msg_success('finalize successed', '')
				})
			})
		}
	}
}
</script>

<style lang="scss" scoped>
	#CrowdFundingDeployed {
		display: block;
		display: -webkit-flex;
		flex-direction: column;
		align-items: center;
		.CrowdFundingItems {
			width: 75%;
			display: flex;
			display: -webkit-flex;
			align-items: center;
			height: 50px;
			padding: 10px;
			margin-top: 30px;
		}
	}
	
	strong {
		width: 300px;
		font-size: 15px;
	}
	
	span {
		width: 100%;
		height: 40px;
		background-color: rgb(255, 255, 255);
		border-radius: 4px;
		border-color: rgb(220, 223, 230);
		border-width: 1px;
		border-style: solid;
		color: rgb(96, 98, 102);
		display: inline-block;
		line-height: 40px;
		padding-left: 15px;
		padding-right: 15px;
	}
	
	div span {
		background-color: #f9f9f9;
		opacity: 1;
	}
	
	.shareholder {
		display: flex;
		display: -webkit-flex;
		justify-content: space-between;
		width: 100%;
	}
	
	.btn-primary {
		color: rgb(255, 255, 255);
		background-color: rgb(186, 27, 33);
		border-color: rgb(186, 27, 33);
	}
	
	.btn {
		margin-bottom: 8px;
		line-height: 32px;
		font-size: 14px;
		padding: 0px 20px;
		border-radius: 3px;
		display: inline-block;
		margin-bottom: 0px;
		font-size: 14px;
		font-weight: 400;
		line-height: 1.42857;
		text-align: center;
		white-space: nowrap;
		vertical-align: middle;
		cursor: pointer;
		user-select: none;
		background-image: none;
		padding: 6px 12px;
		border-width: 1px;
		border-style: solid;
		border-color: transparent;
		border-image: initial;
		border-radius: 4px;
	}
	
	thead {
		display: table-header-group;
		vertical-align: middle;
		border-color: inherit;
	}
	
	.table-bordered {
		border: 1px solid #ddd;
	}
	
	.table {
		width: 100%;
		max-width: 100%;
		margin-bottom: 20px;
	}
	
	table {
		width: 100%;
		table-layout: fixed;
		background-color: transparent;
		border-collapse: collapse;
		border-spacing: 0;
	}
	
	.table-striped>tbody>tr:nth-of-type(odd) {
		background-color: #f9f9f9;
	}
	
	.dialog {
		clear: both;
		.main {
			.lside {
				width: 25%;
				float: left;
				line-height: 40px;
				font-size: 16px;
				font-weight: bold;
				text-align: center;
			}
			.rside {
				width: 75%;
				float: left;
			}
		}
		.line {
			height: 10px;
			width: 100%;
			clear: both;
		}
		.footer {
			padding-top: 15px;
			margin-top: 10px;
			float: right;
		}
	}
</style>


