import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/main_page/HomePage'
import ContractTemplateListPage from '@/main_page/ContractTemplateListPage'
import ContractListPage from '@/main_page/ContractListPage'
import CrowdFunding from '@/contract_templates/CrowdFunding'
import CrowdFundingDeployed from '@/contract_templates/CrowdFundingDeployed'
import Token from '@/contract_templates/Token'
import TokenDeployed from '@/contract_templates/TokenDeployed'
import LoginPage from '@/LoginPage'
import Index from '@/Index'

Vue.use(Router)

export default new Router({
	routes: [{
		path: '/',
		name: 'LoginPage',
		component: LoginPage,
	}, {
		path: '/home',
		name: 'index',
		component: Index,
		children: [{
			path: '',
			name: 'homePage',
			component: HomePage
		}, {
			path: 'contractTemplateListPage',
			name: 'contractTemplateListPage',
			component: ContractTemplateListPage
		}, {
			path: 'contractListPage',
			name: 'contractListPage',
			component: ContractListPage
		}, {
			path: 'crowdFunding',
			name: 'crowdFunding',
			component: CrowdFunding
		}, {
			path: 'crowdFundingDeployed',
			name: 'crowdFundingDeployed',
			component: CrowdFundingDeployed
		}, {
			path: 'token',
			name: 'token',
			component: Token
		}, {
			path: 'tokenDeployed',
			name: 'tokenDeployed',
			component: TokenDeployed
		}]
	}]
})


